import _ from 'lodash'
import BABYLON from 'experiment-babylon-js'
import {
  EventData,
  Array, // eslint-disable-line
  String, // eslint-disable-line
  debugError,
  hasConstructor,
} from 'experiment-js'

import { SmartModal } from 'experiment-boxes'

import {
  addButton,
  highlightPosition,
  setBlackScreen,
  checkIdleAndConnection,
  placePoints,
  updatePoints,
  showPointSpheres,
  hidePointSpheres,
  setViewForDirection,
  showCross,
  hideCross,
  explode } from './functions/globalFunctions'


import { selectLevel, startLevel, endActive } from './states/active'

import { showNextPage, showModal, awakeTutorial, tutorialUnfrozen } from './states/tutorial'

import {
  awakeTryout,
  tryoutEndPrediction,
  tryoutCheckKeyStrokeForPrediction,
  tryoutGoToNextPosition,
  tryoutStartPrediction,
  tryoutEnd,
} from './states/tryout'

import {
  stateUnfrozen,
  startRunning,
  nextTrial,
  goToNextPosition,
  startPrediction,
  checkForClickPrediction,
  checkKeyStrokeForPrediction,
  playSound,
  endObservation,
  pauseTransition,
} from './states/running'

import { awakenPause, endPause, pauseKeyDown } from './states/pause'

import { awakenEnd } from './states/end'

/* =============== Main scene =============== */
export default function MainScene(options = {}) {
  // option base
  const optionsBase = {
    sceneKey: 'main',
    canvasBackground: new BABYLON.Color4(0, 0, 0, 1),
    backgroundRoundRadius: 0,
    clearColor: new BABYLON.Color4(0, 0, 0, 1),
    canvasPercentWidth: 1,
    canvasPercentHeight: 1,
    mode: 'central',
    level: null,
  }

  options = _.extend(optionsBase, options)

  /* --- Get taskObject from context --- */
  const taskObject = this.taskObject

  /* --- Create a basic 3D scene--- */
  const scene = taskObject.create3DScene(options)

  /* --- Add a light --- */
  const light0 = new BABYLON.HemisphericLight('Hemi0', new BABYLON.Vector3(0, 1, 0), scene)
  light0.diffuse = new BABYLON.Color3(1, 1, 1)
  light0.specular = new BABYLON.Color3(1, 1, 1)
  light0.groundColor = new BABYLON.Color3(1, 1, 1)

  /* --- Get stateManager --- */
  const stateManager = scene.stateManager

  /* --- Get canvas --- */
  // const canvas = scene.initialCanvas

  // const camera = scene.initialCamera
  // // // camera.detachControl(taskObject.canvas)
  // camera.radius = 50
  // camera.alpha = 0
  // camera.beta = Math.PI / 2

  const camera = new BABYLON.FreeCamera('OrthoCamera', new BABYLON.Vector3(-50, 0, 0), scene)
  camera.mode = BABYLON.Camera.ORTHOGRAPHIC_CAMERA

  const side = 20
  camera.orthoTop = side
  camera.orthoBottom = -side
  camera.orthoLeft = -side
  camera.orthoRight = side
  //
  //   // This targets the camera to scene origin
  camera.setTarget(new BABYLON.Vector3.Zero())
  //
  scene.activeCamera = camera
  // camera.setPosition(new BABYLON.Vector3(50, 0, 0))

  const R = taskObject.R


  /* --- Load assets --- */

  scene.onResize = []

  stateManager.register(
    addButton,
    highlightPosition,
    setBlackScreen,
    checkIdleAndConnection,
    updatePoints,
    placePoints,
    hidePointSpheres,
    showPointSpheres,
    setViewForDirection,
    showCross,
    hideCross,
    explode)

  stateManager.call('redrawAssets')

  /* --- Setup data --- */
  const dataManager = this.dataManager

  /* --- Add cwData table that will hold a summary of the data --- */
  const cwDataFields = [
    'subject_id',
    'trial',
    'data_index',
    'direction',
    'position',
    'observation_duration',
    'subject_choice', // NA for NOGO
    'correct',
    'response_time', // NA for NOGO or no response
    'key_hits', // ['key', 'time'], [k,t], ...
    'clicks',
    'has_paused',
    'start_observation',
    'end_observation',
    'responded_at',
  ]

  dataManager.addTable('walkerData', cwDataFields)
  dataManager.addTable('tryoutData', cwDataFields)

  let userId = `unknown${this.timeInMs}`
  try {
    userId = dataManager.connections[0].credentials.userId
  } catch (e) {
    debugError(e)
  }

  stateManager.set('subject_id', userId)

  /* --- Add the surveyData table --- */
  const surveyDataFields = [
    'level',
    'similarTask',
    'taskDifficulty',
    'movementWolf',
    'boredom',
    'dependance',
    'repeatsSequence',
    'comments',
  ]

  dataManager.addTable('surveyData', surveyDataFields)

  /* --- Add the logs for each level --- */
  const eventFields = ['id', 'flag', 'happenedAt', 'handledAt', 'data']
  dataManager.addTable('stateRunningEvents', eventFields)
  dataManager.addTable('stateActiveEvents', eventFields)

  /* --- Store scene level inside the stateManager --- */
  stateManager.setGlobal('positionData', {
    indexOnTransitionSequence: -1,
    indexOnPredictionSequence: -1,
    fullSequenceIndex: -1,
  })

  stateManager.setGlobal('hasSeenLearningInfo', false)
  stateManager.setGlobal('hasSeenPredictionInfo', false)
  /* --- Setup states --- */

  /* --- Set state key and store them in the stateManager --- */
  R.add({
    states: {
      running: 'running',
      tutorial: 'tutorial',
      tryout: 'tryout',
      tryoutFMRI: 'tryoutFMRI',
      end: 'end',
    },
    checkpoint: {
      tutorialDone: ['tutorialOneDone', 'tutorialTwoDone'],
      levelDone: ['levelOneDone', 'levelTwoDone', 'levelThreeDone', 'levelFourDone'],
      taskDone: 'taskDone',
      taskEndNoComeback: 'taskEndNoComeback',
    },
    flags: {
      levelDefined: 'levelDefined',
    },
    events: {
      sequenceGenerated: 'sequenceGenerated',
      nextTrial: 'nextTrial',
      goNextPosition: 'goNextPosition',
      endObservation: 'endObservation',
      startPrediction: 'startPrediction',
      responseTimeEnded: 'responseTimeEnded',
      isiAfterLearningEnded: 'isiAfterLearningEnded',
      feedbackEnded: 'feedbackEnded',
      isiAfterFeedbackEnded: 'isiAfterFeedbackEnded',
      pauseTransition: 'pauseTransition',
      forcePause: 'forcePause',
      predictionStartBlackout: 'predictionStartBlackout',
      blackoutEnds: 'blackoutEnds',
      predictionEndsBlackout: 'predictionEndsBlackout',
      isiAfterPredictionEndsBlackout: 'isiAfterPredictionEndsBlackout',
      screenWentBlack: 'screenWentBlack',
      playPredictionSound: 'playPredictionSound',
      playObservationSound: 'playObservationSound',
      showModal: 'showModal',
      goToTutorial: 'goToTutorial',
      hasResponded: 'hasResponded',
      restartTutorial: 'restartTutorial',
      goToLevel1: 'goToLevel1',
      goToLevel2: 'goToLevel2',
      goToLevel3: 'goToLevel3',
      goToLevel4: 'goToLevel4',
      tic: 'tic',
      moreThanFiveNA: 'moreThanFiveNA',
      aknowledgedEnding: 'aknowledgedEnding',
      tutorialNext: 'tutorialNext',
    },
  })

  // eslint-disable-next-line
  const [tutorialState, runningState, tryoutState, endState] = stateManager.addState(R.get.states_tutorial, R.get.states_running, R.get.states_tryout, R.get.states_end)
  const pauseState = stateManager.states.pause

  /* ======== State Active ======== */
  if (options.level !== null) {
    const eventData = new EventData(R.get.flags_levelDefined, scene.stateManager.timeInMs, {
      belongsTo: [
        'globalLog', 'stateActiveEvents',
      ],
      handledAt: null,
      storedAt: null,
      level: options.level,
    })
    stateManager.addEvent(eventData)
  } else {
    stateManager.states.active.addAwakeningFunctions(selectLevel)
  }

  stateManager.states.active.addEndingFunctions(endActive)

  stateManager.states.active.addEventFunctions(R.get.flags_levelDefined, startLevel)
  stateManager.states.active.addEventFunctions(R.get.events_goToTutorial,
    ({ data: { tutorial } }) => {
      stateManager.setGlobal('currentTutorial', tutorial)
      stateManager.goToState(R.get.states_tutorial)
    })

  /* ======== State RUNNING ======== */
  const goToPause = function () {
    if (this.state.shouldPause) {
      this.state.freeze()
      if (hasConstructor(Object, this.taskObject.currentModal) && hasConstructor(SmartModal, this.taskObject.currentModal.modalBox)) {
        this.taskObject.currentModal.modalBox.hide()
      }
      this.stateManager.goToState(this.R.get.states_pause)
    }
  }

  /* --- Register functions --- */
  runningState.addAwakeningFunctions(startRunning)

  runningState.addEventFunctions(R.get.events_nextTrial, nextTrial)
  runningState.addEventFunctions(R.get.events_goNextPosition, goToNextPosition)
  runningState.addEventFunctions(R.get.events_endObservation, endObservation)

  runningState.addEventFunctions(R.get.events_startPrediction, startPrediction)

  runningState.addEventFunctions(R.get.events_pauseTransition, pauseTransition)

  runningState.addEventFunctions(R.get.events_unfrozen, stateUnfrozen)
  runningState.addEventFunctions(R.get.events_forcePause, goToPause)
  runningState.addEventFunctions(R.get.events_windowBlur, goToPause)
  runningState.addEventFunctions(R.get.events_moreThanFiveNA, goToPause)

  runningState.addEventFunctions(R.get.events_click, checkForClickPrediction)
  runningState.addEventFunctions(R.get.events_keydown, checkKeyStrokeForPrediction)

  runningState.addEventFunctions(R.get.events_playPredictionSound, playSound)
  runningState.addEventFunctions(R.get.events_playObservationSound, playSound)

  /* ======= State Running ======= */

  // Center cross
  // Observe movement
  // Respond at any time
  // ITI
  // Center cross


  /* ======= State Tutorial ======= */
  tutorialState.addAwakeningFunctions(awakeTutorial)

  tutorialState.addEventFunctions(R.get.events_tutorialNext, showNextPage)
  tutorialState.addEventFunctions(R.get.events_showModal, showModal)

  tutorialState.addEventFunctions(R.get.events_windowBlur, goToPause)
  tutorialState.addEventFunctions(R.get.events_unfrozen, tutorialUnfrozen)

  tutorialState.addEventFunctions(R.get.events_playPredictionSound, playSound)
  tutorialState.addEventFunctions(R.get.events_playObservationSound, playSound)


  /* ======= State Tryout ======= */
  dataManager.addTable('stateTryout1', eventFields)
  dataManager.addTable('stateTryout2', eventFields)

  tryoutState.addAwakeningFunctions(awakeTryout)
  tryoutState.addEndingFunctions(tryoutEnd)

  tryoutState.addEventFunctions(R.get.events_goNextPosition, tryoutGoToNextPosition)
  tryoutState.addEventFunctions(R.get.events_endObservation, endObservation)

  tryoutState.addEventFunctions(R.get.events_windowBlur, goToPause)
  tryoutState.addEventFunctions(R.get.events_unfrozen, tutorialUnfrozen)

  tryoutState.addEventFunctions(R.get.events_startPrediction, tryoutStartPrediction)
  tryoutState.addEventFunctions(R.get.events_responseTimeEnded, tryoutEndPrediction)
  tryoutState.addEventFunctions(R.get.flags_levelDefined, startLevel)
  tryoutState.addEventFunctions(R.get.events_restartTutorial, () => {
    stateManager.goToState(R.get.states_tutorial)
  })

  tryoutState.addEventFunctions(R.get.events_keydown, tryoutCheckKeyStrokeForPrediction)
  tryoutState.addEventFunctions(R.get.events_click, checkForClickPrediction)

  /* ======== State Pause ======== */
  // stateManager.setPauseKeyStroke()
  pauseState.addAwakeningFunctions(awakenPause)
  pauseState.addEndingFunctions(endPause)
  pauseState.addEventFunctions(R.get.events_keydown, pauseKeyDown)
  pauseState.addEventFunctions(R.get.events_windowFocus, pauseKeyDown)
  // pauseState.addEventFunctions(R.get.flags_levelDefined, startLevel)
  // pauseState.addEndingFunctions(function () { this.stateManager.promise('disposeOfButtons') })

  /* ======= prematureEnd State ======= */
  endState.addAwakeningFunctions(awakenEnd)

  return scene
}



