import _ from 'lodash'
import BABYLON from 'experiment-babylon-js'
import {
  EventData,
  Array,
  String, // eslint-disable-line
  Deferred,
  debuglog,
  debugWarn,
  hasConstructor,
  delay,
} from 'experiment-js'

const placePoints = function (points = []) {
  if (points.length === 0) return

  const stateManager = this.stateManager

  const pointSpheres = stateManager.get('pointSpheres', [])

  let redraw = false
  if (points.length !== pointSpheres.length) {
    redraw = true
  }

  const tempPointSpheres = []
  for (let i = 0; i < points.length; i++) {
    const point = points[i]

    if (hasConstructor(Array, point)) {
      let pointSphere
      if (redraw || pointSpheres.length < i) {
          // draw a sphere at point and store it in the array
        pointSphere = BABYLON.Mesh.CreateSphere(`sphere${i}`, 32, 0.5, this.scene)
      } else {
        pointSphere = pointSpheres[i]
      }

      pointSphere.position = new BABYLON.Vector3(...point)

      tempPointSpheres.push(pointSphere)
    }
  }
  // set the data into the stateManager
  stateManager.set('pointSpheres', tempPointSpheres)
}

const updatePoints = function (points) {
  if (points.length === 0) return

  const stateManager = this.stateManager

  const pointSpheres = stateManager.get('pointSpheres', [])

  if (pointSpheres.length !== points.length) {
    stateManager.call('placePoints', points)
  } else {
    for (let i = 0; i < pointSpheres.length; i++) {
      const point = points[i]
      if (hasConstructor(Array, point)) {
        pointSpheres[i].position = new BABYLON.Vector3(...point)
      }
    }
  }
}

const hidePointSpheres = function () {
  const stateManager = this.stateManager
  const pointSpheres = stateManager.get('pointSpheres', [])

  for (const pointSphere of pointSpheres) {
    if (hasConstructor(BABYLON.Mesh, pointSphere)) {
      pointSphere.visibility = false
    }
  }
}

const showPointSpheres = function () {
  const stateManager = this.stateManager
  const pointSpheres = stateManager.get('pointSpheres', [])

  for (const pointSphere of pointSpheres) {
    if (hasConstructor(BABYLON.Mesh, pointSphere)) {
      pointSphere.visibility = true
    }
  }
}

const setViewForDirection = function ({ direction = null }) {
  const scene = this.scene
  const camera = scene.activeCamera

  if (direction === null) {
    return 'GlobalFunctions.setViewForDirection: direction is null'
  }

  const xPos = (direction === 'LEFT') ? -50 : 50
  camera.position = new BABYLON.Vector3(xPos, 0, 0)
  camera.setTarget(new BABYLON.Vector3.Zero())
  return 'View changed'
}

const addButton = function (options = null) {
  if (typeof this.taskObject === 'undefined') {
    throw new Error('stateManager.addButton: this.taskObject is undefined')
  }

  const stateManager = this.stateManager
  const canvas = stateManager.get('elements2D').canvas

  const baseOptions = {
    id: `button${stateManager.timeInMs}`,
    text: 'text',
    x: 50,
    y: 50,
    width: 100,
    height: 50,
    fill: BABYLON.Canvas2D.GetSolidColorBrush(new BABYLON.Color4(0.8, 0.8, 0.8, 1)),
    clickEventData: null,
    fontName: '30pt Arial',
    baseOpacity: 0.8,
    hoverOpacity: 1,
    zOrder: -0.5,
    marginAlignment: null,
    fontSignedDistanceField: true,
    margin: {},
    padding: {},
    parent: canvas,
  }

  baseOptions.margin.leftPixels = 0
  baseOptions.margin.rightPixels = 0
  baseOptions.margin.topPixels = 0
  baseOptions.margin.bottomPixels = 0
  baseOptions.padding.leftPixels = 0
  baseOptions.padding.rightPixels = 0
  baseOptions.padding.topPixels = 0
  baseOptions.padding.bottomPixels = 0
  baseOptions.margin = _.extend(baseOptions.margin, options.margin)
  baseOptions.padding = _.extend(baseOptions.padding, options.padding)
  options.margin = baseOptions.margin
  options.padding = baseOptions.padding

  // extend options
  options = _.extend(baseOptions, options)

  let buttonOptions = {}
  let margin = {}
  let padding = {}
  if (options.marginAlignment === null) {
    buttonOptions = {
      parent: options.parent,
      id: options.id,
      x: options.x,
      y: options.y,
      width: options.width,
      height: options.height,
      fill: options.fill,
      zOrder: options.zOrder,
      roundRadius: 0,
      children: [new BABYLON.Text2D(options.text, {
        fontName: options.fontName,
        marginVAlignment: 'v: center',
        fontSignedDistanceField: options.fontSignedDistanceField,
        marginHAlignment: 3,
      })],
    }
  } else {
    buttonOptions = {
      parent: options.parent,
      id: options.id,
      width: options.width,
      height: options.height,
      fill: options.fill,
      zOrder: options.zOrder,
      marginAlignment: options.marginAlignment,
      roundRadius: 0,
      children: [new BABYLON.Text2D(options.text, {
        fontName: options.fontName,
        marginVAlignment: 'v: center',
        fontSignedDistanceField: options.fontSignedDistanceField,
        marginHAlignment: 3,
      })],
    }
    margin = options.margin
    padding = options.padding
  }

  // create button and add to canvas
  const buttonRect = new BABYLON.Rectangle2D(buttonOptions)

  buttonRect.opacity = options.baseOpacity

  if (margin !== null) {
    buttonRect.margin.rightPixels = options.margin.rightPixels // TODO make that a specific function in the framework
    buttonRect.margin.leftPixels = options.margin.leftPixels
    buttonRect.margin.topPixels = options.margin.topPixels
    buttonRect.margin.bottomPixels = options.margin.bottomPixels
  }

  if (padding !== null) {
    buttonRect.padding.rightPixels = padding.rightPixels
    buttonRect.padding.leftPixels = padding.leftPixels
    buttonRect.padding.topPixels = padding.topPixels
    buttonRect.padding.bottomPixels = padding.bottomPixels
  }

  // Add an observable for hovering
  buttonRect.pointerEventObservable.add(() => {
    buttonRect.opacity = options.hoverOpacity
  }, BABYLON.PrimitivePointerInfo.PointerOver)

  buttonRect.pointerEventObservable.add(() => {
    buttonRect.opacity = options.baseOpacity
  }, BABYLON.PrimitivePointerInfo.PointerOut)

  // Add an observable for clicking
  if ((options.clickEventData !== null) && (options.clickEventData.constructor === EventData)) {
    buttonRect.pointerEventObservable.add(() => {
      options.clickEventData.happenedAt = stateManager.timeInMs
      options.clickEventData.data.button = buttonRect.id

      stateManager.addEvent(options.clickEventData)
    }, BABYLON.PrimitivePointerInfo.PointerUp)
  }

  return (buttonRect)
}

const setMargins = function (prim, margin) {
  prim.margin.rightPixels = margin.rightPixels // TODO make that a specific function in the framework
  prim.margin.leftPixels = margin.leftPixels
  prim.margin.topPixels = margin.topPixels
  prim.margin.bottomPixels = margin.bottomPixels
}

const showCross = function () {
  const crossSheet = this.taskObject.cloneAssetIntoScene(this.R.get.crossSheet, this.scene)
  crossSheet.hasAlpha = true

  const guiCanvas = this.stateManager.get('GUI')


  let cross = this.stateManager.get('cross', null)
  if (hasConstructor(BABYLON.Sprite2D, cross)) {
    cross.dispose()
  }

  cross = new BABYLON.Sprite2D(crossSheet, {
    parent: guiCanvas,
    id: 'cross',
    marginAlignment: 'v: center, h: center',
    spriteSize: new BABYLON.Size(20, 20),
    spriteLocation: new BABYLON.Vector2(0, 0),
  })

  cross.spriteFrame = 0
  cross.levelVisible = true

  this.stateManager.set('cross', cross)
}

const hideCross = function () {
  const cross = this.stateManager.get('cross', null)

  if (cross !== null) {
    cross.levelVisible = false
  }
}

const highlightPosition = function (options = null) {
  if (typeof this.stateManager === 'undefined') {
    throw new Error('stateManager.highlightPosition: this.stateManager is undefined')
  }

  if (typeof options.position === 'undefined') {
    throw new Error('stateManager.highlightPosition: options.position is undefined')
  }

  return (this.stateManager.promise('drawTiles', {
    hightlight: options.position,
    type: (typeof options.type !== 'undefined') ?
      options.type : 'prediction',
  }))
}

const explode = function ({ meshes = null }) {
  if (meshes === null) {
    return 'No mesh given'
  }
  /* --- Get parameters --- */
  const parameters = this.R.get.parameters

  const explosionTexture = this.taskObject.cloneAssetIntoScene(this.R.get.explosionTexture, this.scene)
  explosionTexture.hasAlpha = true

  for (const mesh of meshes) {
    const particleSystem = new BABYLON.ParticleSystem('particles', 1000, this.scene)
    particleSystem.particleTexture = explosionTexture
    particleSystem.emitter = mesh
    particleSystem.minEmitBox = new BABYLON.Vector3(0, 0, 0)
    particleSystem.maxEmitBox = new BABYLON.Vector3(0, 0, 0)
    particleSystem.color1 = new BABYLON.Color4(0.7, 0.8, 1.0, 0.8)
    particleSystem.color2 = new BABYLON.Color4(0.2, 0.5, 1.0, 0.7)
    particleSystem.colorDead = new BABYLON.Color4(0, 0, 0.2, 0.0)
    particleSystem.minSize = parameters.minSize
    particleSystem.maxSize = parameters.maxSize
    particleSystem.minLifeTime = parameters.minLifeTime
    particleSystem.maxLifeTime = parameters.maxLifeTime
    particleSystem.emitRate = parameters.emitRate
    particleSystem.blendMode = BABYLON.ParticleSystem.BLENDMODE_ONEONE
    particleSystem.gravity = new BABYLON.Vector3(0, 0, 0)
    particleSystem.direction1 = new BABYLON.Vector3(-5, 5, 5)
    particleSystem.direction2 = new BABYLON.Vector3(5, -5, -5)
    particleSystem.minAngularSpeed = 0
    particleSystem.maxAngularSpeed = Math.PI
    particleSystem.minEmitPower = parameters.minEmitPower
    particleSystem.maxEmitPower = parameters.maxEmitPower
    particleSystem.updateSpeed = 0.025

    particleSystem.targetStopDuration = 0.5
    particleSystem.disposeOnStop = true

    particleSystem.start()
  }

  return 'Exploded'
}

const setBlackScreen = function (options = null) {
  const baseOptions = { // TODO create a defaultOptions()
    on: true,
  }

  options = _.extend(baseOptions, options)
  const elements2D = this.stateManager.getGlobal('elements2D')
  const canvas = elements2D.canvas

  if (!_.has(elements2D, 'blackScreen')) {
    const blackScreen = new BABYLON.Rectangle2D({
      parent: canvas,
      id: 'blackScreen',
      position: new BABYLON.Vector2(0, 0),
      width: canvas.size.width,
      height: canvas.size.height,
      zOrder: 0,
    })

    elements2D.blackScreen = blackScreen
  }

  if (options.on) {
    elements2D.blackScreen.opacity = 1
  } else {
    elements2D.blackScreen.opacity = 0
  }
}

const checkIdleAndConnection = function ({ deferred = new Deferred(), returnToState = null } = { deferred: new Deferred(), returnToState: null }) {
  const stateManager = this.stateManager
  const R = this.R
  const dataManager = this.dataManager
  const connection = this.connection

  const shouldCheckTime = stateManager.get('shouldCheckTime', false)

  if (shouldCheckTime) {
    return dataManager.query('getLastInteraction', null, connection, deferred).then((r) => {
      const delay = stateManager.timeInMs - r.lastInteraction
      const delayMin = delay / (60000)
      if (delay > 20 * 60 * 1000) {
        debugWarn('running.goToNextPosition: idle for too long')
        stateManager.set('endReason', `You were idle for more than 20 min: ${delayMin} min`)
        stateManager.goToState(R.get.states_end)
      } else if ((returnToState !== null) && (stateManager.currentStateKey !== returnToState)) { stateManager.goToState(returnToState) }
    })
    .catch(() => {
      debugWarn('running.goToNextPosition: lost connection')
      returnToState = returnToState || stateManager.currentStateKey

      if (stateManager.currentStateKey !== R.get.states_idle) {
        stateManager.call('hideAll')
        stateManager.onNext(this.R.get.events_unfrozen, () => { stateManager.call('showAll') })
        this.state.freeze()
        stateManager.goToState(R.get.states_idle)
      }

      this.taskObject.modal({ type: 'centralLarge', title: R.get.lostConnectionTitle, content: R.get.lostConnectionContent })
      .then(() => delay(50)).then(() => { stateManager.call('checkIdleAndConnection', { returnToState }) })
    })
  }
  return Promise.resolve()
}

export {
  addButton,
  highlightPosition,
  setMargins,
  setBlackScreen,
  checkIdleAndConnection,
  placePoints,
  updatePoints,
  showPointSpheres,
  hidePointSpheres,
  setViewForDirection,
  showCross,
  hideCross,
  explode }



